html {
  font-size: 16px;
  box-sizing: border-box;
}

body {
  font-family: "Montserrat", sans-serif;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

h3 {
  font-size: 2rem;
  color: #0078B3;
  margin: 0;
}

p {
  margin: 0;
}

