.header {
  margin-top: 1.42rem;
}
@media (min-width: 768px) {
  .header {
    margin-top: 1.125rem;
    margin-left: 1.438rem;
    margin-right: 1.438rem;
  }
}
@media (min-width: 992px) {
  .header {
    margin-right: 10.25rem;
    margin-left: 10.25rem;
  }
}

.navbar {
  display: flex;
  height: 236px;
  margin-left: 1.05rem;
}
.navbar .navbar__bar {
  width: 0.25rem;
  border-radius: 14px;
  color: black;
  background-color: #E9EBEE;
}
@media (min-width: 768px) {
  .navbar {
    margin: 0;
    display: flex;
    flex-direction: column-reverse;
    align-items: space-between;
    padding: 0;
    width: 100%;
    height: 100%;
  }
  .navbar .navbar__bar {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 0.188rem;
    margin-top: 0.25;
  }
}
.navbar .navbar__list {
  list-style: none;
  padding: 0;
  margin: 0;
}
@media (min-width: 768px) {
  .navbar .navbar__list {
    display: flex;
    justify-content: space-between;
  }
}
.navbar .navbar__list .list__bar {
  width: 0.25rem;
  border-radius: 14px;
  color: black;
  background-color: #E9EBEE;
  margin-left: -0.25rem;
}
.navbar .navbar__list .list__bar--active {
  background-color: #0D1C3D;
}
.navbar .navbar__list .list__item {
  margin: 1rem 0;
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .navbar .navbar__list .list__item {
    display: flex;
    flex-direction: column;
    margin: 0;
  }
  .navbar .navbar__list .list__item .list__bar {
    margin: 0;
    width: 100%;
    height: 0.188rem;
    margin-bottom: -0.188rem;
  }
  .navbar .navbar__list .list__item .list__link:first-child {
    margin-left: 0;
  }
}
.navbar .navbar__list .list__item:first-child {
  margin-top: 0;
}
.navbar .navbar__list .list__item:last-child {
  margin-bottom: 0;
}
.navbar .navbar__list .list__item .list__link {
  margin-left: 0.81rem;
  text-decoration: none;
  color: #0078B3;
  line-height: 1.25rem;
  letter-spacing: 0.8px;
  font-size: 0.9rem;
  font-weight: bold;
}
.navbar .navbar__list .list__item .list__link--active {
  color: #0D1C3D;
}

@media (min-width: 992px) {
  .main__container {
    margin: 0 8.9rem;
  }
}

.container {
  width: 95%;
  display: none;
}
.container--active {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 0.5fr 2fr 3fr;
  row-gap: 1rem;
  align-items: center;
  justify-content: center;
}
.container--active .container__title {
  margin-left: 1.438rem;
}
.container--active .container__text {
  margin-left: 1.75rem;
  width: 95%;
  height: 100%;
  font-size: 0.875rem;
  line-height: 1.25rem;
}
.container--active .container__img {
  margin-left: 0.813rem;
  margin-right: 1.438;
  height: 100%;
  grid-row: 2/3;
}
.container--active .container__img .img__text {
  text-align: center;
  font-size: 0.875rem;
  line-height: 0.75rem;
  margin-top: 0.75rem;
}
@media (min-width: 768px) {
  .container--active {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 0.2fr 3fr;
    align-items: flex-start;
    column-gap: 1.563rem;
    margin: 0;
    margin-top: 1.18rem;
    width: 100%;
    row-gap: 0;
  }
  .container--active .container__img {
    grid-column: 2/2;
    grid-row: 1/3;
    margin-right: 1.438rem;
  }
  .container--active .container__text {
    grid-column: 1/2;
    margin-top: 0.938rem;
  }
}
@media (min-width: 992px) {
  .container--active {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    column-gap: 0;
  }
  .container--active .container__img {
    grid-column: 3/3;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
  }
  .container--active .container__img .img__text {
    text-align: center;
    max-width: 60%;
  }
  .container--active .container__text {
    grid-column: 1/3;
  }
}

.img .img__picture > img {
  display: block;
  height: auto;
  max-width: 100%;
  width: 100%;
}